/* Container for Contact section */
.contact-container {
    max-width: 800px;
    margin: 50px auto; 
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    font-size: calc(5px + 1vmin);
    padding-top: 100px;
}
.contact-details {
    flex: 2; 
}
