.portfolio-container {
    max-width: 1000px;
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.portfolio-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
    width: 100%;
    padding-top: 150px;
}

.portfolio-item {
    position: relative; /* Enable positioning for overlay */
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    opacity: 1;

}

.portfolio-item:hover {
    transform: scale(1.05);
    opacity: 1;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    cursor: pointer;
}

.portfolio-item img {
    width: 100%;
    height: 100%;
    max-height: 350px;
    object-fit: cover; /* Ensures the image covers the container without distortion */
}
.project-title-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 20%;
    background: rgba(255, 255, 255, 1); /* Opaque white background */
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0; /* Hidden by default */
    transition: opacity 0.3s ease;
}

/* Title styling */
.project-title-overlay h3 {
    color: #333;
    font-size: 1.5rem;
    text-align: center;
}

/* Show the overlay on hover */
.portfolio-item:hover .project-title-overlay {
    opacity: 1;
}


/* Media query for mobile */
@media (max-width: 768px) {
    .portfolio-grid {
        grid-template-columns: 1fr; /* One column for mobile */
        padding-top: 150px; /* Add padding to top for spacing */
    }

    .portfolio-item img {
        max-width: 70%; /* Adjust max width on mobile */
        margin: 0 auto; /* Center the image */
    }

}