.navbar {
    height: 7rem;
    width: 100vw;
    margin: 0 auto;
    padding: 2rem 1rem;
    padding-right: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
    /* background-color: #282c34; */

}
.menuItem{
    padding: 25px;
    font-size: calc(5px + 2vmin);

    
}
.menuItem a:hover {
    cursor: pointer;
}

.logo-container {
    display: flex;
    align-items: center;
    font-size: calc(20px + 2vmin);
    /* padding-left: 2rem; */

}
.logo-container:hover {

        /* box-shadow: 0 2px 5px rgba(0,0,0,0.1); */
    }


.logo {
    height: 50px;
}

.desktopMenu {
    display: flex;
    background-color: #ffffff;
    /* visibility: hidden; */

    padding: 50px;
    padding-top: 80px;

}

.menuButton {
    background-color: #0e53df;
    color: #522222;
    border: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
    display: none; /* Hide the desktop menu button by default */
}

.mobileMenuButton {
    background-color: #ffffff;
    color: #ca1313;
    border: none;
    padding: 0.5rem 2rem;
    padding-right: 100px;
    cursor: pointer;
    display: none; /* Hide the mobile menu button by default */
}

@media (max-width: 800px) {
    .desktopMenu {
        display: flex; 
        flex-direction: column;/* Hide the desktop menu on mobile */
    }

    .menuButton {
        display: none; /* Still hidden */
    }

    .mobileMenuButton {
        display: block; /* Show the mobile menu button */
        width: 100px;
        height: 100px;
        font-size: 3rem;

    }

    .menu {
        position: absolute;
        top: 7rem;
        left: 0;
        width: 50%;
        /* background-color: #fff; */
        overflow: hidden;
        max-height: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        /* box-shadow: 0 2px 5px rgba(0,0,0,0.1); */
        transition: max-height 0.3s ease-out;
    }

    .menu.open {
        max-height: 700px; /* Adjust this value based on your content height */

        display: flex;
width: 50%;


    }

    .menu div {
        padding: 1rem 0;
        width: 100%;
        text-align: center;
    }
}