/* Container for the whole About section */
.about-container {
    max-width: 1000px;
    margin: 50px auto; 
    padding: 20px;
    display: flex;
    flex-direction: row; 
    justify-content: space-between;
    align-items: flex-start;
    font-size: calc(5px + 1vmin);
    padding-top: 100px;
}

/* Container for the profile picture */
.profile-picture-container {
    flex: 1; 
    margin-right: 20px; 
}

/* Profile picture styling */
.profile-picture {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 50%; /* Make the image circular */
}

/* Container for the about details */
.about-details {
    flex: 3; /* Take up more space than the profile picture */
}

/* Skills section */
.skills-list {
    list-style-type: none; /* Remove default bullet points */
    padding: 0;
}

.skills-list li {
    margin-bottom: 5px; /* Add some space between each skill */
}